"use client";
import React, { useState } from "react";

export default function Register() {
  const [form, setForm] = useState({
    name: "",
    department: "",
    bio: "",
    specialization: "",
    password: "",
    email: "",
    otp: "",
  });

  const [otpSent, setOtpSent] = useState(false);
  const [sendLoading, setSendLoading] = useState(false);
  const [verifyLoading, setVerifyLoading] = useState(false);(false);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const sendOtp = async (e: React.FormEvent) => {
    e.preventDefault();
    setSendLoading(true);

    try {
      await fetch("/api/send-otp", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email: form.email }),
      });
      setOtpSent(true);
    } catch (error) {
      console.error("OTP send error", error);
    }

    setSendLoading(false);
  };

  const verifyOtp = async (e: React.FormEvent) => {
    e.preventDefault();
    setVerifyLoading(true);

    try {
      await fetch("/api/verify-otp", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form),
      });
      alert("Registration successful!");
    } catch (error) {
      console.error("OTP verification error", error);
    }

    setVerifyLoading(false);
  };

  return (
    <div className="bg-gradient-to-r from-indigo-50 to-blue-50 min-h-screen flex flex-col">
      <header className="bg-gradient-to-r from-indigo-600 to-blue-600 text-white text-center py-4 shadow-md">
        <h1 className="text-2xl font-bold">ProfConnect - First Time Registration</h1>
      </header>

      <main className="flex-grow flex items-center justify-center p-6">
        <form
          onSubmit={otpSent ? verifyOtp : sendOtp}
          className="bg-white shadow-lg rounded-2xl p-8 w-full max-w-md space-y-6"
        >
          {/* Name */}
          <div>
            <label className="block font-medium mb-1 text-gray-700">Full Name</label>
            <input
              name="name"
              type="text"
              value={form.name}
              onChange={handleChange}
              placeholder="Enter full name"
              className="w-full border rounded-lg px-3 py-2 focus:ring focus:ring-indigo-200"
              required
            />
          </div>

          {/* Department */}
          <div>
            <label className="block font-medium mb-1 text-gray-700">Department</label>
            <input
              name="department"
              type="text"
              value={form.department}
              onChange={handleChange}
              placeholder="Enter department"
              className="w-full border rounded-lg px-3 py-2 focus:ring focus:ring-indigo-200"
              required
            />
          </div>

          {/* Bio */}
          <div>
            <label className="block font-medium mb-1 text-gray-700">Bio</label>
            <input
              name="bio"
              type="text"
              value={form.bio}
              onChange={handleChange}
              placeholder="Short bio"
              className="w-full border rounded-lg px-3 py-2 focus:ring focus:ring-indigo-200"
              required
            />
          </div>

          {/* Specialization */}
          <div>
            <label className="block font-medium mb-1 text-gray-700">Specialization</label>
            <input
              name="specialization"
              type="text"
              value={form.specialization}
              onChange={handleChange}
              placeholder="Enter specialization"
              className="w-full border rounded-lg px-3 py-2 focus:ring focus:ring-indigo-200"
              required
            />
          </div>

          {/* Password */}
          <div>
            <label className="block font-medium mb-1 text-gray-700">Password</label>
            <input
              name="password"
              type="password"
              value={form.password}
              onChange={handleChange}
              placeholder="Enter password"
              className="w-full border rounded-lg px-3 py-2 focus:ring focus:ring-indigo-200"
              required
            />
          </div>

          {/* Email */}
          <div>
            <label className="block font-medium mb-1 text-gray-700">Email</label>
            <input
              name="email"
              type="email"
              value={form.email}
              onChange={handleChange}
              placeholder="Enter email"
              className="w-full border rounded-lg px-3 py-2 focus:ring focus:ring-indigo-200"
              required
            />
          </div>

          {/* Send OTP Button */}
          {!otpSent && (
            <button
              type="submit"
              className="w-full bg-indigo-600 hover:bg-indigo-700 text-white px-6 py-2 rounded-lg transition"
            >
              {sendLoading ? "Sending..." : "Send OTP"}
            </button>
          )}

          {/* OTP Section */}
          {otpSent && (
            <div className="space-y-4">
              <div>
                <label className="block font-medium mb-1 text-gray-700">Enter OTP</label>
                <input
                  name="otp"
                  type="text"
                  value={form.otp}
                  onChange={handleChange}
                  placeholder="Enter OTP received on email"
                  className="w-full border rounded-lg px-3 py-2 focus:ring focus:ring-indigo-200"
                  required
                />
              </div>

              <button
                type="submit"
                className="w-full bg-green-600 hover:bg-green-700 text-white px-6 py-2 rounded-lg transition"
              >
                {verifyLoading ? "Validating..." : "Validate OTP & Complete Registration"}
              </button>

              <button
                type="button"
                onClick={sendOtp}
                className="w-full bg-indigo-500 hover:bg-indigo-600 text-white px-6 py-2 rounded-lg transition"
              >
                {sendLoading ? "Sending... ‚è≥" : "Resend OTP"}
              </button>
            </div>
          )}
        </form>
      </main>
    </div>
  );
}
